<template>
  <div id="fix">
    <div class="signInWarp" v-show="signShow">
      <div class="signIn">
        <div class="signText">修改密码</div>
        <div class="inputWarp">
          <el-input placeholder="请输入账号" v-model="input1" clearable class="itemInput">
            <template slot="prepend">账号</template>
          </el-input>
          <i v-if="input1E" class="el-icon-close inputState inputStateE"></i>
          <i v-if="input1S" class="el-icon-check inputState inputStateS"></i>
        </div>
        <div class="inputWarp">
          <el-input type="password" placeholder="请输入旧密码" v-model="input2" clearable class="itemInput">
            <template slot="prepend">旧密码</template>
          </el-input>
          <i v-if="input2E" class="el-icon-close inputState inputStateE"></i>
          <i v-if="input2S" class="el-icon-check inputState inputStateS"></i>
        </div>
        <div class="inputWarp">
          <el-input type="password" placeholder="请输入新密码" v-model="input3" clearable class="itemInput">
            <template slot="prepend">新密码</template>
          </el-input>
          <i v-if="input3E" class="el-icon-close inputState inputStateE"></i>
          <i v-if="input3S" class="el-icon-check inputState inputStateS"></i>
        </div>
        <div class="inputWarp">
          <el-input type="password" placeholder="请再输入新密码" v-model="input4" clearable class="itemInput">
            <template slot="prepend">确认</template>
          </el-input>
          <i v-if="input4E" class="el-icon-close inputState inputStateE"></i>
          <i v-if="input4S" class="el-icon-check inputState inputStateS"></i>
        </div>
        <el-button type="primary" @click="signClickFn" :disabled="openBtn">提交</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { baseUrl } from '@/api/unsplash/baseUrl'
export default {
  name: 'Sign',
  data () {
    return {
      signShow: true,
      input1: '',
      input1E: false,
      input1S: false,
      input2: '',
      input2E: false,
      input2S: false,
      input3: '',
      input3E: false,
      input3S: false,
      input4: '',
      input4E: false,
      input4S: false,
      imgSrc: '',
      code_id: '',
      baseUrl: baseUrl
    }
  },
  computed: {
    openBtn: function () {
      return !(this.input1 !== '' && this.input2 !== '' && this.input3 !== '' && this.input3 === this.input4)
    }
  },
  watch: {
    input3: function (val) {
      if (this.input3 !== '' && this.input3 === this.input4) {
        this.input4S = true
        this.input4E = false
        return true
      } else if (this.input4 !== '' && this.input3 !== this.input4) {
        this.input4S = false
        this.input4E = true
      } else {
        this.input4S = false
        this.input4E = false
      }
    },
    input4: function (val) {
      if (this.input3 !== '' && this.input3 === this.input4) {
        this.input4S = true
        this.input4E = false
        return true
      } else if (this.input4 !== '' && this.input3 !== this.input4) {
        this.input4S = false
        this.input4E = true
      } else {
        this.input4S = false
        this.input4E = false
      }
    }
  },
  created: function () {
  },
  methods: {
    // randomPassword (size) {
    //   let seed = ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J', 'K', 'L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'U', 'V', 'W', 'X', 'Y', 'Z', 'a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'i', 'j', 'k', 'm', 'n', 'p', 'Q', 'r', 's', 't', 'u', 'v', 'w', 'x', 'y', 'z', '2', '3', '4', '5', '6', '7', '8', '9']
    //   let seedlength = seed.length
    //   var createPassword = ''
    //   for (let i = 0; i < size; i++) {
    //     createPassword += seed[Math.floor(Math.random() * seedlength)]
    //   }
    //   return createPassword
    // },
    // loadImgFn () {
    //   this.code_id = this.randomPassword(24)
    //   this.imgSrc = this.baseUrl + '/codes?code_id=' + this.code_id
    // },
    // verificationClickFn () { // click change verification
    //   this.loadImgFn()
    // },
    signClickFn () { // click to signIn
      this.signInfn()
    },
    async signInfn () {
      try {
        let params = {
          login_name: this.input1,
          oldpwd: this.input2,
          newpwd: this.input3
        }
        let res = await this.$api.unsplash.fix(params)
        if (res.errno === '0') {
          this.$router.push({path: '/'})
          if (res.data) {
            // this.$router.push({path: '/reference/theme'})
          }
        } else if (res.errno === '4106') {
          this.input2E = true
          this.input2S = false
          let self = this
          setTimeout(function () {
            self.input2E = false
            self.input2S = false
          }, 3000)
        } else {
          this.$alert('服务器错误', '警告', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async identifyfn () {
      try {
        let params = {
          'captcha': this.input3,
          'code_id': this.code_id
        }
        let res = await this.$api.unsplash.identify(params)
        console.log(res)
        if (res.msg === '通过') {

        } else {
          this.$alert('服务器错误', '警告', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        }
      } catch (e) {
        console.log(e)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  #fix{
    .signInWarp{
      min-width: 100vw;
      min-height: 100vh;
      position: absolute;
      top: 0;left: 0;
      z-index: 100;
      background: #b7bcbf;
      .signIn{
        width: 340px;
        height: 540px;
        padding: 40px;
        background: #c5c8ce;
        position: absolute;
        top: 50%;left: 50%;
        transform: translate(-50%, -50%);
        overflow: hidden;
        &::before{
          content: '';
          display: inline-block;
          width: 260%;
          height: 150%;
          border-radius: 50%;
          background: #ffffff;
          position: absolute;
          top: 50%;left: 50%;
          transform: translate(-50%, 14%);
        }
        .signText{
          height:60px;
          line-height:60px;
          font-size: 22px;
          font-weight: bold;
          letter-spacing: 4px;
          color: #ffffff;
          margin-bottom: 15px;
        }
        .itemInput{
        }
      }
      .inputWarp{
        position: relative;
        margin-bottom: 15px;
        .el-input-group__prepend {
          width: 85px;
        }
        .inputState{
          position: absolute;
          top: 50%;
          right: 0;
          transform: translate(120%, -50%);
        }
        .inputStateE{
          color: red;
        }
        .inputStateS{
          color: green;
        }
      }
      .el-button--primary.is-disabled, .el-button--primary.is-disabled:active, .el-button--primary.is-disabled:focus, .el-button--primary.is-disabled:hover {
        color: #FFF;
        background-color: #409EFF!important;
        border-color: #409EFF!important;
      }
    }
  }
</style>



